import axios from 'axios';
import { useEffect,useState } from 'react';
import '../Component/MovieCard.scss'
import styled from 'styled-components';
import { Input, Row, Col } from 'antd';
import {LeftCircleOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

export default function Mypage(){
    const [id,setId] = useState('');
    const [items,setItems] = useState([])
    const URL = 'https://movieclient-server.herokuapp.com'
    // let replaceTitle = title.replace(/(<([^>]+)>)/gi, "")
    const myPage = async()=> {
        try{
            const res = await axios.get('/naver/mypage',{
                withCredentials: true
            })
        if(res && res.status ===200){
            console.log(res.data);
            setId(res.data.id)
            setItems(res.data.favorite)
            console.log(res.data.id);
            console.log(items);
        }
        }catch(err){
        console.log(err);
    }
    }

    useEffect(()=> {
        myPage()
    },3000)
    // console.log(items);
    const navigate = useNavigate();

    return(
        <>
         <div className='back-icon'><LeftCircleOutlined style={{color:'white', fontSize:'25px', marginTop:'10px', marginLeft:'25px'}}onClick={()=> navigate(-1)}/></div>
        <div style={{color:'white',textAlign:'center', fontSize:'20px', paddingTop:'25px'}}>{id}  .</div>
        <div style={{color:'white',textAlign:'center', fontSize:'16px', paddingTop:'50px'}}>   </div>
             <Row>
             {items &&items.map((item) => {
                 return (
                 <Col xs={24} sm={12} md={12} lg={8} xl={6}>
                    <div className="movie-card-container" key={item}>
                    <CardImg image={item.image} />
                    <div className="movie-text">
                    <h2>{item.title.replace(/(<([^>]+)>)/gi, "")}</h2>
                    <div>{`${item.pubDate}`}</div>
                    <div className="movie-summary-row">
                    <h5></h5>
                    </div>
                    <div className="movie-likes">{`${item.userRating}/ 10`}</div>
                    <div className='mb-5'>: {item.actor}</div>
                    <div className='mb-5'>: {item.director}</div>
                    <a href={item.link} target="_blank">{item.link}</a>
                    </div>
        </div>
                </Col>
                 );
             })}
             </Row>
        </>
    )
}
const CardImg = styled.div`
  width: 100%;
  height: 200px;
  background-image: url(${(props)=> props.image});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 111 !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(0.35, rgba(0, 0, 0, 1)),
    color-stop(0.5, rgba(0, 0, 0, 1)),
    color-stop(0.65, rgba(0, 0, 0, 1)),
    color-stop(0.85, rgba(0, 0, 0, 0.6)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  position: relative;
`;