import React,{useState} from 'react';
import axios from 'axios';
import '../Component/Form.scss'
import {  Navigate, useNavigate,Link } from 'react-router-dom';
export default function Register(){
    const URL = 'https://movieclient-server.herokuapp.com'
    const navigate = useNavigate();
    const [id,setId] = useState('');
    const [pw,setPw] = useState('');
    const [modal,setModal] = useState(false)
    const [already,setAlready] = useState(false)
    const handleId = (e) => {
        setId(e.target.value)
    }
    const handlePw = (e) => {
        setPw(e.target.value)
    }
    const replaceId = /^[a-zA-Z0-9]*$/;
    const register = async() => {
        if ( !replaceId.test(id)){
            alert(', .')
        }
        let body = {
            id: id,
            pw: pw
        }
        try {
            const res = await axios.post('/naver/register',body,{
                withCredentials: true
              })
            if( res && res.data.message =='success'){
                console.log(res);
                setModal(true)
                setAlready(false)
                console.log(res.data.message);
                alert(' .')
            }else if (res && res.data.message =='fail'){
                console.log(res);
                setAlready(true)
                setModal(false)
            }
        }
        catch(error){
            if(error) console.log(error);
        }
    }
    return (
        <div id="login_wrapper">
		<h1>Register</h1>
		<div className='form'>
			<div>
				<label>Id</label>
				<input type="text" id="id" name="id" maxlength="20" placeholder="username" required onChange={handleId}/>
			</div>
			<div>
				<label>Password</label>
				<input type="text" id="pwd" name="pw" maxlength="20" placeholder="password" required onChange={handlePw}/>
			</div>
            {
                modal === true ? <Modal/> : null
            }
            {
                already === true ? <Already/> : null
            }
			<button type="submit" id="btn_login" onClick={register}></button>
		</div>
	</div> 
        //     ,  
    )
}
                      
function Modal(){
    return(
         <>
        <button className='modal'><Link to='/'> </Link></button>
    </>
        )
    }

function Already(){
    return(
        <div style={{color:'white'}}>   .</div>
    )
}