import React,{useState,useEffect} from 'react';
import axios from 'axios'
import { Link, redirect, useNavigate } from 'react-router-dom';
import '../Component/Form.scss'
export default function Login(){
    const URL = 'https://movieclient-server.herokuapp.com'
    const [id,setId] = useState('');
    const [pw,setPw] = useState('');
    const [modal,setModal] = useState(false)
    const navigate = useNavigate();

    const handleId = (e) => {
        setId(e.target.value)
    }
    const handlePw = (e) => {
        setPw(e.target.value)
    }
    
    const login = async() => {
        let body = {
            id: id,
            pw: pw
        }
        try {
            const res = await axios.post('/naver/login',body,{
                withCredentials:true
            })
            if(res.data.message=='fail'){
                navigate('/login')
                console.log(res);
                alert('  , .')
            }if (res && res.data.message =='success'){
                console.log(res);
                alert(' ')
                setModal(true)
            }
        }
        catch(error){
            console.log(error)
        }
    }

    return (
    <div id="login_wrapper">
		<h1>Login</h1>
		<div className='form'>
			<div>
				<label>Id</label>
				<input type="text" id="id" name="id" maxlength="20" placeholder="username" required onChange={handleId}/>
			</div>
			<div>
				<label>Password</label>
				<input type="text" id="pw" name="pw" maxlength="20" placeholder="password" required onChange={handlePw}/>
			</div>
			<button id="btn_login" onClick={()=> login()}></button>
      {
        modal ===true ? <Modal/> : null
      } 
		</div>
	</div>  
    )
}
function Modal(){
    return(
         <>
        <button className='modal'><Link to='/'> </Link></button>
    </>
        )

}