import React, { useContext } from 'react';
import styled from 'styled-components';
import App from '../App';
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import {LeftCircleOutlined } from '@ant-design/icons';
import '../Component/MovieDetail.scss'

function MovieDetail({movie}) {
  const navigate = useNavigate();
  const URL = 'https://movieclient-server.herokuapp.com'
  // console.log(item);

  const {title,link,image,pubDate,userRating,actor,director} = movie
    // const {title,link,image,pubDate,userRating} = item
    let replaceTitle = title.replace(/(<([^>]+)>)/gi, "")
  const addMypage = async()=> {
    try{
      const res = await axios.post('/naver/mypage',{
        favorite: movie
      },{
        withCredentials: true
      })
      if(res.data ==" "){alert('   .')}
      if(res.data== '  .'){
        alert('  .')
      }if(res.data.message == 'success'){
        alert('  .')
        console.log(res);
      }
    }catch(error){
      if(error) console.log(error);
    }
  }

  return (
    <>
    <div className='back-icon'><LeftCircleOutlined style={{color:'white', fontSize:'25px', marginTop:'25px', marginLeft:'25px'}}onClick={()=> navigate(-1)}/></div>
    <div className="movie-detail-card-container">
      <CardImg image={image} />
      <div className="movie-text">
        <h2>{replaceTitle}</h2>
        <div>{`${pubDate}`}</div>
        <div className="movie-summary-row">
          <h5></h5>
        </div>
        <div className="movie-likes">{`${userRating}/ 10`}</div>
        <div className='mb-5'>: {actor}</div>
        <div className='mb-5'>: {director}</div>
        <a href={link} target="_blank">{link}</a>
        <button className="movie-add-btn"onClick={addMypage}></button>
        {/*   mypage    */}
      </div>
    </div>
    </>
  );
};

const CardImg = styled.div`
  width: 100%;
  height: 400px;
  background-image: url(${(props)=> props.image});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 111 !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(0.35, rgba(0, 0, 0, 1)),
    color-stop(0.5, rgba(0, 0, 0, 1)),
    color-stop(0.65, rgba(0, 0, 0, 1)),
    color-stop(0.85, rgba(0, 0, 0, 0.6)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  position: relative;
`;
export default MovieDetail;