import { configureStore,createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name : 'user',
    initialState : 'kim',
    reducers : {
        changeName(state){
          return 'john ' + state
        }
      }
  })

let isLoggined = createSlice({
  name:'isLoggined',
  initialState: null,
  reducers: {
    changeLogin(state,action){
      state = action.payload
    }
  }
})

export let { changeName } = user.actions 
export let {changeLogin} = isLoggined.actions
export default configureStore({
    reducer: {
        user: user.reducer,
        isLoggined: isLoggined.reducer
    }
}) 

