import axios from 'axios'
import react,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import './MovieMenu.scss'
export default function Menu(){
    const URL = 'https://movieclient-server.herokuapp.com'
    const [islogin,setIslogin] = useState(false)
    const checkLogin = async()=> {
        try{
            const res = await axios.get('/naver/login',{
              withCredentials: true
            })
            // console.log(res);
            if(res.data.isLogin == true){
                setIslogin(true)
            }
        }catch(err){
            console.log(err);
        }
    }
    useEffect(()=> {
        checkLogin()
        // console.log(islogin);
    })

    const logout = async()=> {
        try{
          const res = await axios.get('/naver/logout')
          // console.log(res);
          window.location.reload()
        }catch(err){
          console.log(err);
        } 
      }
      return(
        <div style={{display:'flex'}} className='menu-container'>
        <p onClick={()=> { window.location.reload()}}><Link to='/'></Link></p>
        <p><Link to='/login'></Link></p>
        <p><Link to='/register'></Link></p>
        {
          islogin === true ? <><p><Link to='/mypage'></Link></p><p onClick={logout}><Link to='/'></Link></p></> : null
        } 
      </div>
      )
}