import React, { Fragment,useState,useEffect } from 'react';
import './App.css';
import 'antd/dist/antd.css';
import MovieDetail from './Container/MovieDetail';
import {BrowserRouter as Router,Routes,Route} from 'react-router-dom'
import MovieCard from './Component/MovieCard';
import { Input, Row, Col } from 'antd';
import axios from 'axios';
import Login from './Container/Login'
import Register from './Container/Register'
import Mypage from './Container/Mypage';
import Menu from './Component/Menu'
const { Search } = Input;
axios.defaults.withCredentials = true;
function App() {

  const [query, setQuery] = useState('');
    const handleQuery = (e) => {
      setQuery(e.target.value);
      // console.log(query);
    };
    const [items,setItems] = useState(null);
    const [movie, setMovie] = useState(null)
    const newitem = (x) => {
      setMovie(x)
      // console.log(movie);
    }
    const URL = 'https://movieclient-server.herokuapp.com'
    // get    items   
    const handleButton = async () => {
      try {
        const res = await axios.get('/naver/getNaverMovie',{
          params: {
            query:query
          }
        },{
          withCredentials: true
        })
        if (res && res.status === 200) {
          const { data } = res;
          console.log(data.items);
          setItems(data.items);
        }
      } catch (error) {
        console.log(error);
      }
    }
   
  return (
    <Router>
    <Routes>
      <Route path='/' element={
        <Fragment>
          <Menu/>
        <div
            style={{ display: 'flex', justifyContent: 'center', padding: '2rem' }}
        >
            <input
            placeholder="  !"
            // onSearch={(value) => console.log(value)}
            onChange={handleQuery}
            // onClick={handleButton}
            style={{ width: 200, height:34}}
            />
            <button onClick={handleButton} className='search-btn'></button>
        </div>
        <div>
            <Row>
            {items &&items.map((item,i) => {
                return (
                <Col xs={24} sm={12} md={12} lg={8} xl={6} key={i}>
                    <MovieCard item={item} newitem={newitem}></MovieCard>;
                </Col>
                );
            })}

            </Row>
        </div>
        </Fragment>
      }/>
      <Route path='/MovieDetail' element={<MovieDetail movie={movie}/>}/>
      <Route path='/login' element={<Login />}/>
      <Route path='/register' element={<Register/>}/>
      <Route path='/mypage' element={<Mypage/>}/>
    </Routes>
    </Router>
  );
}

export default App;
