import React,{useState} from 'react';
import './MovieCard.scss'
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import axios from 'axios'
const MovieCard = ({item, newitem}) => {
    const navigate = useNavigate();
    const {title,link,image,pubDate,userRating} = item
    let replaceTitle = title.replace(/(<([^>]+)>)/gi, "")
    

    const moveToDetail = () => {
      navigate('/MovieDetail')
      // console.log(item);
      newitem(item)
    }
  return (
    <>
    <div className="movie-card-container" onClick={()=>{moveToDetail()}}>
      <CardImg image={image}/>
      <div className="movie-text">
        <h2>{replaceTitle}</h2>
        <div>{`${pubDate}`}</div>
        <div className="movie-summary-row">
          <h5></h5>
        </div>
        <div className="movie-likes">{`${userRating}/ 10`}</div>
        <div className='btn-container'><button className='movie-detail-btn'></button></div>
      </div>
    </div>
   
    </>
  );
};

// .. card       
//       data   ..?
// card moviesearchcontainer  item props  
//    detail state     
//  click       detail get  
//     
const CardImg = styled.div`
  width: 100%;
  height: 300px;
  background-image: url(${(props)=> props.image});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 111 !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(0.35, rgba(0, 0, 0, 1)),
    color-stop(0.5, rgba(0, 0, 0, 1)),
    color-stop(0.65, rgba(0, 0, 0, 1)),
    color-stop(0.85, rgba(0, 0, 0, 0.6)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  position: relative;
`;
export default MovieCard;